<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-item>
        <ec-panel-item-left>
          <ec-header title="罚没烟码包分配领用" icon="onetomany" color-code="008" />
        </ec-panel-item-left>
      </ec-panel-item>
    </ec-container-header>
    <ec-container-body :auto="false">
      <ec-v-layout>
        <ec-v-item>
          <ec-panel>
            <ec-panel-body>
              <a-descriptions bordered :column="4">
                <a-descriptions-item label="调拨年度">
                  {{ queryItems.tranYear }}
                </a-descriptions-item>
                <a-descriptions-item label="烟叶类型">
                  {{
                    getLabelName(
                      queryItems.leafTypeCode,
                      this.leafTypeList,
                      'leafTypeCode',
                      'leafTypeName',
                    )
                  }}</a-descriptions-item
                >
                <a-descriptions-item label="烟叶形态">
                  {{
                    getLabelName(
                      queryItems.leafFormCode,
                      this.leafFormList,
                      'leafFormCode',
                      'leafFormName',
                    )
                  }}
                </a-descriptions-item>
                <a-descriptions-item label="申请码包数量">
                  {{ totalData.realApplyNum }}
                </a-descriptions-item>

                <a-descriptions-item label="申请量">
                  {{ totalData.applyNum }}
                </a-descriptions-item>
                <a-descriptions-item label="分配码包数量"> </a-descriptions-item>
                <a-descriptions-item label="领用码包数量"> </a-descriptions-item>
                <a-descriptions-item label=""> </a-descriptions-item>
              </a-descriptions>
            </ec-panel-body>
          </ec-panel>
          <ec-panel>
            <ec-panel-toolbar>
              <ec-query-panel
                :colCount="colCount"
                @query="onQuery"
                @reset="onReset"
                :defaultFold="true"
              >
                <ec-query-item label="调拨年度">
                  <a-input-number
                    placeholder="请输入"
                    v-model="queryItems.tranYear"
                  ></a-input-number>
                </ec-query-item>
                <ec-query-item label="领用单位">
                  <org-picker
                    @ok="selectOrg"
                    v-model="queryItems.applyOrgCodes"
                    placeholder="请选择"
                  />
                </ec-query-item>
                <ec-query-item label="仓库">
                  <a-select allowClear v-model="queryItems.storeCode" placeholder="请选择">
                    <a-select-option
                      v-for="item in storesList"
                      :key="item.storeCode"
                      :value="item.storeCode"
                      >{{ item.storeName }}</a-select-option
                    >
                  </a-select>
                  <!-- <org-picker v-model="orgCodeList" placeholder="请选择" /> -->
                </ec-query-item>
                <template #inner>
                  <ec-query-item label="烟叶类型">
                    <ec-select
                      @select="leafTypeChange"
                      showSearch
                      allowClear
                      placeholder="请选择"
                      v-model="queryItems.leafTypeCode"
                    >
                      <a-select-option
                        v-for="leafTypeEk in leafTypeList"
                        :key="leafTypeEk.leafTypeCode"
                      >
                        {{ leafTypeEk.leafTypeName }}
                      </a-select-option>
                    </ec-select>
                  </ec-query-item>
                  <ec-query-item label="烟叶形态">
                    <ec-select
                      showSearch
                      allowClear
                      placeholder="请选择"
                      v-model="queryItems.leafFormCode"
                    >
                      <a-select-option
                        v-for="item in leafFormList"
                        :key="item.leafFormCode"
                        :value="item.leafFormCode"
                        >{{ item.leafFormName }}</a-select-option
                      >
                    </ec-select>
                  </ec-query-item>
                </template>
              </ec-query-panel>
              <ec-divider />
            </ec-panel-toolbar>

            <ec-panel-body height="100%">
              <ec-v-layout>
                <!-- 表格工具栏 -->
                <ec-v-item>
                  <ec-panel-item>
                    <ec-panel-item-left>
                      <!-- <a-button @click="onExport">导出</a-button> -->
                    </ec-panel-item-left>
                    <ec-panel-item-right>
                      <a-space :size="15">
                        <!-- </ec-query-item> -->

                        <!-- <ec-query-item label="面积单位"> -->
                        <span
                          >重量单位：<ec-select
                            showSearch
                            allowClear
                            placeholder="请选择"
                            v-model="weightUnit"
                            style="width: 150px"
                          >
                            <a-select-option
                              v-for="weight in weightUnitList"
                              :key="weight.weightUnitCd"
                              :value="weight.weightUnitCd"
                            >
                              {{ weight.weightUnitName }}
                            </a-select-option>
                          </ec-select></span
                        >

                        <!-- </ec-query-item> -->
                      </a-space>
                    </ec-panel-item-right>
                  </ec-panel-item>
                </ec-v-item>
                <ec-v-item> </ec-v-item>
                <!-- 列表 -->
                <ec-v-item>
                  <ec-table
                    ref="table"
                    border
                    @page-action="handlePagination"
                    :pagination="!Object.keys(orgInfo).length ? {} : pagination"
                    :data="tableData"
                    :loading="loading"
                    highlight-current-row
                  >
                    <ec-table-col type="checkbox" width="50" align="center"></ec-table-col>
                    <ec-table-col title="序号" type="seq" width="70" align="center" />
                    <ec-table-col field="tranYear" title="调拨年度" align="left" width="200" />
                    <ec-table-col field="applyOrgName" title="领用单位" align="left" width="200" />
                    <!-- <ec-table-col field="applyDate" title="申请日期" align="left" width="200" /> -->
                    <!-- <ec-table-col field="applyOrgName" title="申请单位" align="left" width="200" /> -->
                    <ec-table-col field="storeName" title="仓库" align="left" width="200" />
                    <ec-table-col
                      field="leafTypeName"
                      title="烟叶类型"
                      align="left"
                      min-width="150"
                    />
                    <ec-table-col
                      field="leafFormName"
                      title="烟叶形态"
                      align="left"
                      min-width="150"
                    />
                    <ec-table-col
                      field="applyNum"
                      title="申请码包数"
                      align="left"
                      min-width="150"
                    />
                    <ec-table-col field="applyNum" title="申请量" align="left" min-width="150" />
                    <ec-table-col
                      title="操作"
                      fixed="right"
                      scopedSlots
                      align="center"
                      min-width="150"
                    >
                      <template #default="{ row }">
                        <a-button type="link" @click="onDetails(row)">详细</a-button>
                      </template>
                    </ec-table-col>
                  </ec-table>
                </ec-v-item>
              </ec-v-layout>
            </ec-panel-body>
          </ec-panel>
        </ec-v-item>
      </ec-v-layout>
      <!-- <execution-manage-form @onQuery="onQuery" ref="ExecutionManageForm"></execution-manage-form>
      <detail ref="Detail"> </detail> -->
      <ec-drawer
        title="详细"
        :is-confirm="true"
        size="small"
        :visible="visible"
        @close="visible = false"
      >
        <ec-container height="auto" fixed="top bottom" color-code="007">
          <ec-container-body>
            <ec-panel class="prodDrawer" color-code="001" title="分配明细">
              <ec-panel-item>
                <ec-panel-item-right>
                  <a-space :size="15" style="margin-right: 15px"
                    >调拨年度: {{ queryItems.tranYear }}</a-space
                  >
                  <a-space :size="15">重量单位: {{ '担' }}</a-space>
                </ec-panel-item-right>
              </ec-panel-item>
              <ec-panel-item>
                <ec-table ref="table" border :data="detailsTableData" :loading="detailsLoading">
                  <ec-table-col title="序号" type="seq" width="70" align="center" />
                  <ec-table-col
                    min-width="150"
                    field="leafTypeName"
                    title="烟叶类型"
                    align="left"
                  />
                  <ec-table-col
                    min-width="150"
                    field="leafFormName"
                    title="烟叶形态"
                    align="left"
                  />
                  <ec-table-col min-width="150" field="packType" title="包装类型" align="left" />
                  <ec-table-col field="spec" title="规格 (KG/件)" align="left" min-width="150" />
                  <ec-table-col
                    field="allotNum"
                    title="分配码包数量"
                    align="left"
                    min-width="150"
                  />
                  <ec-table-col
                    field="collectNum"
                    title="领用码包数量"
                    align="left"
                    min-width="150"
                  />
                </ec-table>
              </ec-panel-item>
            </ec-panel>
          </ec-container-body>
          <ec-container-footer fixed>
            <a-space>
              <a-button @click="visible = false">关闭</a-button>
            </a-space>
          </ec-container-footer>
        </ec-container>
      </ec-drawer>
    </ec-container-body>
  </ec-container>
</template>

<script>
// import ExecutionManageForm from './ExecutionManageForm.vue';
// import Detail from './Detail.vue';
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'penaltyReceive',
  components: {
    // ExecutionManageForm,
    // Detail,
    OrgPicker,
  },
  computed: {
    _api() {
      return this.$apis.pcm.penatly.apply;
    },
  },
  data() {
    return {
      visible: false,
      applyOrgCode: '',
      topInfo: {},
      orgInfo: {},
      storesList: [],
      objectData: {},
      leafFormList: [],
      busiYear: '',
      detailsTableData: [],
      detailsLoading: false,
      queryItems: {
        applyOrgCodes: [],
        storeCode: '',
        tranYear: undefined,
        leafTypeCode: undefined,
        busiYear: undefined,
        leafFormCode: undefined,
      },
      weightUnit: 50,
      areaUnit: 1,
      colCount: 5,
      nowYear: '',
      loading: false,
      leafTypeCd: '',
      leafTypeList: [],
      oldDataRow: [], // 拷贝的旧数据，单位切换的时候要在旧的数据上转换
      oldTotalData: [], // 拷贝的旧上级数据，单位切换的时候要在旧的数据上转换
      totalData: {}, // 合计的数据
      isNullData: [],
      workSubmitState: [
        { id: '', name: '全部' },
        { id: '0', name: '未提交' },
        { id: '1', name: '已提交' },
      ],
      workAuditState: [
        { id: '', name: '全部' },
        { id: '0', name: '未审批' },
        { id: '1', name: '处长审批' },
        { id: '2', name: '副总经理审批' },
        { id: '3', name: '完成' },
      ],
      workOptType: [
        { id: '', name: '全部' },
        { id: '0', name: '调减' },
        { id: '1', name: '调增' },
      ],
      weightUnitList: [
        // { weightUnitCd: 1, weightUnitName: '公斤', isSelected: false },
        { weightUnitCd: 50, weightUnitName: '担', isSelected: false },
      ],

      orgCodeList: [],
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
    };
  },
  async created() {
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM'; // 获取当前登入信息
    await this.getCurrentUserOrg();
    await this.handleGetLeafTypeList();
    await this.leafTypeChange(this.queryItems.leafTypeCode);
    await this.onQuery();
  },
  methods: {
    selectOrg(data) {
      const { selectOption } = data;
      console.log(this.queryItems.applyOrgCode);
      this.getCmTStoreByOrgCode(this.queryItems.applyOrgCode);
      this.queryItems.orgName = selectOption.title;
    },
    getLabelName(code, array = [], key, labelName) {
      if (!code || !array.length) return '';
      const obj = array.find((item) => item[key] === code);
      return obj[labelName] || '';
    },
    onDetails(row) {
      this.detailsLoading = true;
      this.queryItems.applyOrgCode = this.queryItems.applyOrgCodes.length
        ? this.queryItems.applyOrgCodes.join('')
        : '';
      const data = { applyType: 'CONF_CP', barcodeApplyId: row.barcodeApplyId, ...this.queryItems };
      this._api
        .queryDetail(data)
        .then((res) => {
          this.detailsTableData = res || [];
        })
        .finally(() => {
          this.detailsLoading = false;
        });
      this.visible = true;
    },

    async handlePagination(pageNumber, pageSize) {
      this.pagination.pageNumber = pageNumber;
      this.pagination.pageSize = pageSize;
      this.onQuery();
    },
    // 获取当期用户信息
    getCurrentUserOrg() {
      this._api.getCurrentUserOrg({ dataState: 1 }).then((data) => {
        this.orgInfo = data || {};
        this.queryItems.applyOrgCodes = data ? [data.orgUniqueCode] : undefined;
        this.queryItems.applyOrgCode = this.queryItems.applyOrgCodes.length
          ? this.queryItems.applyOrgCodes.join('')
          : '';
        this.getCmTStoreByOrgCode(this.queryItems.applyOrgCode);
        // this.queryItems.orgName = data.orgName;
      });
    },

    // 获取仓库
    async getCmTStoreByOrgCode(orgCode = undefined) {
      this._api
        .getCmTStoreByOrgCode({ dataState: 1, orgCode })
        .then((data) => {
          this.storesList = data.rows || [];
        })
        .finally(() => {
          this.storesList.unshift({
            storeName: '全部',
            storeCode: '',
          });
          this.queryItems.storeCode = '';
        });
    },
    // 获取烟叶类型数据
    async handleGetLeafTypeList() {
      this.queryItems.tranYear = new Date().getFullYear();
      if (!this.leafTypeList?.length) {
        const res = await this.$apis.bpm.executionEnter.queryLeafType({});
        this.leafTypeList = res?.rows || [];
        this.leafTypeList.some((item) => {
          if (item.leafTypeName === '烤烟') {
            this.leafTypeCd = item.leafTypeCode;
            this.queryItems.leafTypeCode = item.leafTypeCode;
            return true;
          } else {
            return false;
          }
        });
      }
      // this.onQuery();
    },
    async leafTypeChange(data) {
      await this._api.spinnerLeafForm({ leafTypeCodes: [data] }).then((info) => {
        this.leafFormList = info?.rows;
        this.leafFormList.some((item) => {
          if (item.leafFormCode === '101') {
            // this.leafTypeCd = item.leafTypeCode;
            this.queryItems.leafFormCode = item.leafFormCode;
            console.log(this.queryItems.leafFormCode);
            return true;
          } else {
            return false;
          }
        });
      });
    },
    // 数据初始化
    async onQuery() {
      this.loading = true;
      this.queryItems.applyOrgCode = this.queryItems.applyOrgCodes.length
        ? this.queryItems.applyOrgCodes.join('')
        : '';
      const data = {
        applyType: 'CONF_CP',
        pageNo: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
        ...this.queryItems,
      };
      console.log(data);
      this._api
        .pageConfCp(data)
        .then((res) => {
          const rows = res.rows || [];
          this.pagination.total = res.totalRows;
          this.oldDataRow = JSON.parse(JSON.stringify(rows));
          this.tableData = rows;
          console.log(`列表的数据：${JSON.stringify(this.tableData)}`);
        })
        .finally(() => {
          this.loading = false;
          this.selectedRows = [];
        });
      this._api.queryHead(data).then((info) => {
        this.topInfo = { ...info };
      });
    },

    // 导出
    onExport() {
      // const data = this.handleData();
      // data.type = 'add';
      // this.$refs.ExecutionEnterAdjustForm.show(data);
    },

    // 重置
    async onReset() {
      let leafTypeCode;
      this.leafTypeList.some((item) => {
        if (item.leafTypeName === '烤烟') {
          this.leafTypeCd = item.leafTypeCode;
          leafTypeCode = item.leafTypeCode;
          return true;
        } else {
          return false;
        }
      });
      this.queryItems = {
        applyOrgCodes: this.orgInfo ? [this.orgInfo.orgUniqueCode] : undefined,
        applyOrgCode: this.queryItems.applyOrgCodes.length
          ? this.queryItems.applyOrgCodes.join('')
          : '',
        storeCode: '',
        tranYear: new Date().getFullYear(),
        leafTypeCode,
        leafFormCode: undefined,
      };

      this.queryItems.leafTypeCode = this.leafTypeList.length
        ? this.leafTypeList[0].leafTypeCode
        : '';
      await this.leafTypeChange(this.queryItems.leafTypeCode);
      this.queryItems.submitState = '';
      this.queryItems.auditState = '';
      this.queryItems.oprType = '';
      this.queryItems.orgName = '';

      this.$refs.table.pageAction(1);
    },
  },
};
</script>
<style lang="less" scoped>
.year {
  font-size: 18px;
  line-height: 22px;

  color: #18191a;
  font-weight: 700;
  padding-top: 18px;
  padding-right: 50px;
  font-family: '微软雅黑 Bold', '微软雅黑', 'Microsoft YaHei', sans-serif;
}
.paddingTop {
  padding-top: 15px;
}
</style>
